@use '../inc/vars' as *;

.career {
  background-color: $blue;
  .container {
    z-index: 1;
  }
  .section {
    &__header {
  
    }
    &__title {

    }
    &__header-additional {

    }
    &__subtitle {

    }
    &__body {
      
    }
  }
  &__carousel {
    overflow: visible;

    .swiper-slide {
      max-width: 325px;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        max-width: 275px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    & + .button-container {
      @media(min-width: $md){
        margin-top: 110px;
      }
    }
    .swiper {
      &__nav {
        display: flex;
        width: 118px;
        height: 118px;
        background: $text-light;
        border-radius: 100%;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        position: absolute;
        top: 0;
        bottom: 0;
        right: -15px;
        z-index: 1;
        margin: auto;
        @media(max-width: $xl){
        
        }
        @media(max-width: $lg){
        
        }
        @media(max-width: $md){
          display: none;
        }
        @media(max-width: $sm){
        
        }
        @media(max-width: $xs){
        
        }
        
      }
      &__nav-el {
        width: 50%;
        height: 100%;
        display: flex;
        cursor: pointer;
        transform: none;
        &.swiper-button-disabled {
          opacity: 0.5;
          cursor: default;

        }
        &::before {
          content: '';
          width: 15px;
          height: 15px;
          border: 1px solid $text;
          transform: rotate(45deg);
          margin: auto;
          display: flex;
        }
        &--prev {
          &::before {
            margin-right: 0;
            border-top: none;
            border-right: none;
          }
        }
        &--next {
          &::before {
            margin-left: 0;
            border-bottom: none;
            border-left: none;
          }
        }
      }
    }
    .swiper-pagination {
      @media(min-width: $md){
        display: none;
      }
    }
  }
  &--hero {
    background-color: $green;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      flex-direction: column;
      padding-bottom: 0;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    .decor {
      background-image: url(../img/svg/decor-career-hero.svg) !important;
      height: 70vh;
      right: -40vh !important;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        position: relative;
        right: 0 !important;
        height: auto;
        max-width: 325px;
        background-position: right bottom;
        left: auto;
        margin-right: 0;
        margin-left: auto;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
        right: 0 !important;
      }
      
    }
  }


  .decor {
    background-image: url(../img/svg/decor-career.svg);
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      right: -100px !important;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
      background-position: right bottom;
      right: 0;
      left: auto;
    }
    
    
  }
}